import Vue from 'vue';
import VueRouter from 'vue-router'

Vue.use(VueRouter)
const routes = [
    {
        path: '/',
        component: () => import('../views/navbar.vue'),

    }, {
        path: '/about',
        name: 'about',
        component: () => import('../views/about.vue')
    },
    {
        path: '/xufuji',
        name: 'xufuji',
        component: () => import('../views/xufuji.vue')
    }, {
        path: '/licheng',
        name: 'licheng',
        component: () => import('../views/licheng.vue')
    }, {
        path: '/hunqing',
        name: 'hunqing',
        component: () => import('../views/hunqing.vue')
    }, {
        path: '/detail/:id',
        name: 'detail',
        component: () => import('../views/detail.vue')
    },
]
const router = new VueRouter({
    routes
})
export default router