<template>
  <div class="boxs">
    <el-menu
      :default-active="activeIndex"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      :menu-trigger="types"
      active-text-color="#ffd04b"
      router
    >
      <el-menu-item class="logoOne"
        ><img src="../assets/logo.png" alt="" class="logo"
      /></el-menu-item>

      <el-menu-item index="/about">简介</el-menu-item>
      <el-submenu index="2">
        <template slot="title">产品</template>
        <el-menu-item index="/xufuji" route="/xufuji">徐福记</el-menu-item>
        <el-menu-item index="/licheng" route="/licheng">力诚</el-menu-item>
        <el-menu-item index="/hunqing" route="/hunqing">婚庆用品</el-menu-item>
      </el-submenu>
      <el-menu-item index="/">主页</el-menu-item>
    </el-menu>
  </div>
</template>
<script>
export default {
  name: "Nav",
  data() {
    return {
      activeIndex: "1",
      types: "", //设备类型
    };
  },
  watch: {
    $route(to, from) {
      // 对路由变化作出响应...
      console.log(to, from, 111);
      // to就代表你要到那个path路径里边去  from代表你从那个path路径来
    },
  },
  created() {
    // 判断是手机还是PC 手机用click触发 PC用hover触发
    this.types = localStorage.getItem("types") === "pc" ? "hover" : "click";
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
  },
};
</script>
<style scoped lang="scss">
.boxs {
  width: 100%;

  background: #fff;
}
.el-menu {
  max-width: 1080px;
  margin: 0 auto;
}
.logoOne {
  float: left !important;
}
.logo {
  width: 32px;
  height: 32px;
}
.el-menu--horizontal > .el-menu-item {
  float: right;
}
.el-menu--horizontal > .el-submenu {
  float: right;
}
</style>